/**
 * imui.Form.Label
 * @author moxhe
 * @date 2016-08-07
 */

import React, { PropTypes } from 'react';
import classnames from 'classnames';

export default function Label(props) {
  const { className, prefixCls, required, help, htmlFor } = props;

  const clsStr = {
    [className]: !!className,
    [prefixCls]: true
  };

  return (
    <label htmlFor={htmlFor} className={classnames(clsStr)}>
      {required ? <span className={`${prefixCls}-required`}>*</span> : null}
      {props.children}
      {help ? <span className={`${prefixCls}-help`}>[<a href={help}>?</a>]</span> : null}
    </label>
  );
}

Label.propTypes = {
  htmlFor: PropTypes.any.isRequired,
  help: PropTypes.string,
  required: PropTypes.bool
};

Label.defaultProps = {
  prefixCls: 'im-label'
};
